// Colors
// ---------------------------------

// Default

$color-default-black:               #2d383f;
$color-default-white:               #ffffff;

// Project palette
//     https://www.htmlcsscolor.com/hex/334482

$color-button-default:              #0266c1;
$color-button-text:                 #2d383f;

$color-background-default:          #ffffff;

$color-icon-default:                #2d383f;
$color-icon-additional:             #0266c1;
$color-icon-light:                  #ffffff;

$color-overlay-big-slider:           linear-gradient(143deg, rgba(15, 20, 41, 0.33) 0%, rgba(15, 20, 41, 0) 100%);
$color-overlay-tour-card:            linear-gradient(0deg, rgba(0, 0, 0, 0.6) 61.90%, rgba(0, 0, 0, 0) 100%);
$color-overlay-menu:                 rgba(0, 0, 0, 0.5);

// Typography
// ---------------------------------

//    html   
$fz-default:                        16px;

//     ios     
// https://www.artlebedev.ru/kovodstvo/sections/159/#13
$ff-montserrat:                         "Montserrat", "Arial", sans-serif;

// Animation
// ---------------------------------

$tf-default:                        ease;
$trans-default:                     0.3s $tf-default;

//      $tf-default,     
$trans-modal:                       0.6s cubic-bezier(0.55, 0, 0.1, 1);

$trans-600:                         0.6s $tf-default;

// Viewports
// ---------------------------------

$vp-768:                            768px;
$vp-1200:                           1200px;
